import { useState } from 'react';
import CustomDatePicker from './components/CustomDatePicker';
import CustomSlider from './components/CustomSlider';
import { format, differenceInDays, addDays, getYear, startOfYear, endOfYear } from "date-fns";
import './App.css';

function App() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(addDays(new Date(), 365));
  const [ratio, setRatio] = useState(0.7);
  const [amount, setAmount] = useState(''); // Nowy stan dla wartoci kwotowej

  const handleStartDateChange = (date) => {
    setStartDate(date);
  };

  const handleEndDateChange = (date) => {
    setEndDate(date);
  };

  const handleRatioChange = (event, newValue) => {
    setRatio(newValue);
  };

    const handleAmountChange = (event) => {
    setAmount(event.target.value);
  };

  const startOfYearDate = startOfYear(endDate);
  const endOfYearDate = endOfYear(startDate);
  const daysLeftInYear = differenceInDays(endOfYearDate, startDate);
  const daysAll = differenceInDays(endDate, startDate) + 1;

  const isSameYear = getYear(startDate) === getYear(endDate);
  const daysAfterNewYear = isSameYear ? 0 : differenceInDays(endDate, startOfYearDate) + 1;

  return (
    <>
      <div className="App">
        <h1>DLA JOLI</h1>
        <p>Data pocztkowa :</p>
        <CustomDatePicker date={startDate} onChange={handleStartDateChange} />

        <p>Data kocowa :</p>
        <CustomDatePicker date={endDate} onChange={handleEndDateChange} />

        <p>Ilo wszystkich dni: {daysAll}</p>
        <p style={{ color: 'blue' }}>
          Ilo dni do koca roku: <strong>{daysLeftInYear}</strong> - Ilo dni po nowym roku: <strong>{daysAfterNewYear}</strong>
        </p>

        <p>Wspczynnik: {ratio}</p>
        <div className="custom-slider-container">
          <CustomSlider
            ratio={ratio}
            onChange={handleRatioChange}
          />
        </div>
        
        <p>Wpisz kwot:</p>
        <input
          type="number"
          value={amount}
          onChange={handleAmountChange}
          placeholder="Wpisz kwot"
        />
        <br/>

        <p>Kwota na kady dzie: {amount / daysAll} z</p>
        <p>Kwota na kady dzie po zaokrgleniu : { (amount / daysAll).toFixed(2) } z</p>


        <p>Kwota na stary rok: {(amount / daysAll) * daysLeftInYear} z</p>
        <p>Kwota na nowy rok: {(amount / daysAll) * daysAfterNewYear} z</p>

      </div>
    </>
  );
}

export default App;